@import url(https://fonts.googleapis.com/css?family=PT+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'PT Sans', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: pink;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: rgb(247, 26, 199);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wrapper {
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 50px 10%;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}
.nav-pills {
    position: fixed;
    top: 0;
    left: 0;
    background: #e416c1;
    min-height: 65px;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px #e416c1, 0 10px 10px #e416c1;
    color: #fff;
    font-size: 2em;
    font-weight: bold;
    z-index: 99;
    white-space: nowrap;
}

.nav-pills a {
    color: #fff;
}

.nav-pills a:hover {
    color: #fff;
    background-color: #e416c1 !important;
}

.desc-normal {
    color: #fff;
}

.desc-correct {
    color: #0f0;
}

.desc-incorrect {
    color: #f00;
}


.titlebox {
    background-color: pink;
    background-size: 100%;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 280px;
    text-align: center;
    vertical-align: center;
    font-family: 'Holtwood One SC', serif;
    color: #fff;
    text-shadow: 0 0 5px #000;
    margin-bottom: 20px;
}

.titlebox h1 {
    margin-top: 90px;
    font-size: 3.5em;
}

.titlebox p {
    font-size: 1.8em;
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 300px;
    overflow: hidden;
    margin: 1rem;
    position: relative;
    width: 270px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}

.card > .img-container {
    width: 100%;
    height: auto;
    overflow: hidden;
    text-align: center;
}
.card > .img-container img {
    margin-top: 10px;
    width: 100%;
    height: 210px;
}

.style_prevu_kit
{
    display:inline-block;
    border:0;
    width:196px;
    height:210px;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1);
    transition: all 200ms ease-in;
    transform: scale(1);   

}
.style_prevu_kit:hover
{
    box-shadow: 0px 0px 150px #000000;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.5);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.5);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.5);
    transition: all 200ms ease-in;
    transform: scale(1.5);
}


.style_prevu_kit_ex {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}


