.nav-pills {
    position: fixed;
    top: 0;
    left: 0;
    background: #e416c1;
    min-height: 65px;
    width: 100%;
    text-align: center;
    -webkit-box-shadow: 0 14px 28px #e416c1, 0 10px 10px #e416c1;
    box-shadow: 0 14px 28px #e416c1, 0 10px 10px #e416c1;
    color: #fff;
    font-size: 2em;
    font-weight: bold;
    z-index: 99;
    white-space: nowrap;
}

.nav-pills a {
    color: #fff;
}

.nav-pills a:hover {
    color: #fff;
    background-color: #e416c1 !important;
}

.desc-normal {
    color: #fff;
}

.desc-correct {
    color: #0f0;
}

.desc-incorrect {
    color: #f00;
}

